      "en": {
        "title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Skills"])},
        "subtitle": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Tools, languages and other things that I like to work with"])}
      },
      "ru": {
