export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "en": {
        "title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Hi all, I'm Polina"])},
        "subtitle": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["A passionate Frontend Developer having an experience of building Web applications with TypeScript / JavaScript / Vuejs / Nuxtjs and some other cool libraries and frameworks."])},
        "contact": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["contact me"])},
        "resume": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["resume"])}
      },
      "ru": {
        "title": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Привет, я Полина!"])},
        "subtitle": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Увлеченный фронтенд разработчик, имеющий опыт создания веб-приложений с помощью TypeScript / JavaScript / Vuejs / Nuxtjs и некоторых других интересных библиотек и фреймворков."])},
        "contact": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["связаться"])},
        "resume": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["резюме"])}
      }
    }
  })
}
